.App {
  font-family: sans-serif;
}

html {
  width: 100%;
  height: 100%;
  background-color: #e29837;
}

.background-color {
  background-color: #4b3832;
}

.display-background-color {
  background-color: #4b3832;
}

.centered-fully {
  margin: 0;
  background: #e9cba4;
  border: 1px solid #854442;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-85%, -50%);
  height: 40%;
  width: 150%;
}

.center-text-in-div {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bigger-font {
  font-size: 30px;
}
